.grid {
  display: flex;
  flex-direction: column;
  width: 100%;

  @include onTablet {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }

  @include onDesktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
  }

  &--desktop-tablet {
    display: flex;

    @include onTablet {
      display: grid;
    }

    @include onDesktop {
      display: grid;
    }
  }

  &--desktop {
    display: flex;

    @include onDesktop {
      display: grid;
    }
  }

  &__item {
    @include onTablet {
      @for $start from 1 through 6 {
        @for $end from $start through 6 {
          &--#{$start}-#{$end}-tablet {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    @include onDesktop {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &--#{$start}-#{$end}-desktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
